:root {
  --light: azure;
  --dark: #0d1d2a;
  --min-height: 100svh;
  --content-width: 45rem;
  --header-font: "JetBrains Mono", monospace;
  --body-font: "Inter", sans-serif;
  --hover-transition: background-color .2s, border .2s, color .2s, fill .2s;
  --padding-default: 2rem;
  --margin-small: 2rem;
  --margin-large: 3rem;
}

html {
  transition: all .5s;
}

html[data-theme="light"] {
  --background-color: var(--light);
  --font-color: var(--dark);
  --pink: #f87060;
  --red: #650c00;
  --green: #5ab80d;
  --blue: #2441a9;
  --gray: #888;
  --button: #0d1d2a;
  --toast: #193549;
  --button-color: var(--background-color);
  --box-shadow: #0000001a 0px 10px 15px -3px, #0000000d 0px 4px 6px -2px;
}

html[data-theme="dark"] {
  --background-color: var(--dark);
  --font-color: var(--light);
  --pink: #f87060;
  --red: #fe4b34;
  --green: #c6eda6;
  --blue: #7d99ff;
  --gray: #bbb;
  --button: var(--blue);
  --toast: #193549;
  --button-color: var(--background-color);
  --box-shadow: #7a7a7a1a 0px 1px 3px 0px, #7a7a7a0f 0px 1px 2px 0px;
}

@media (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark only;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

html, html:focus-within {
  scroll-behavior: smooth;
}

ul:not([role="list"]), ol:not([role="list"]), ul[role="menu"], ol[role="menu"] {
  padding: 0;
  list-style: none;
}

ul[role="menu"] a, ol[role="menu"] a {
  text-decoration: none;
}

body {
  min-height: var(--min-height);
  text-rendering: optimizespeed;
  background-color: var(--background-color);
  color: var(--font-color);
  line-height: 1.5;
  font-family: var(--body-font), sans-serif;
  flex-direction: column;
  margin: 0;
  display: flex;
}

h1, h2, h3, h4, h5, h6, ul, ol, p, figure, blockquote, dl, dd {
  margin: 0 0 1.6rem;
}

a {
  color: var(--blue);
  font-weight: 500;
}

a:active {
  outline: none;
}

a:visited {
  color: var(--blue);
}

a:hover, a:focus, a:focus-visible {
  color: var(--green);
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  box-shadow: var(--box-shadow);
  border-radius: .5rem;
  max-width: 100%;
  display: block;
}

svg {
  fill: currentColor;
}

.mode-icon {
  width: 2rem;
}

svg:not(:root) {
  overflow: hidden;
}

input, button, textarea, select {
