  }

  footer .container {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }
}

@media only screen and (width >= 65rem) {
  .container {
    --content-width: 65rem;
    --margin-small: 3rem;
    --margin-large: 4rem;
  }

  header .headshot {
    border-radius: 50%;
    align-self: center;
    max-width: 8rem;
    margin: 1.5rem 0 2rem;
  }

