.skip-links {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.skip-links a {
  background-color: var(--background-color);
  color: var(--font-color);
  z-index: 100000;
  padding: 1rem;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  top: -100rem;
}

.skip-links a:focus-visible {
  top: 0;
}

:focus-visible {
  outline: .3rem solid var(--pink);
  outline-offset: .1rem;
}

.to-top {
  background-color: var(--background-color);
  color: var(--font-color);
  border: .2rem solid var(--font-color);
  border-bottom: none;
  border-radius: .5rem .5rem 0 0;
  padding: .5rem .75rem;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: background-color .2s, color .2s, opacity .2s, transform .2s;
  position: fixed;
  bottom: 0;
