}

.to-top.visible, .to-top:focus-visible, .to-top.visible:focus-visible {
  transform: none;
}

#toast {
  --toast-accent: var(--green);
  background-color: var(--toast);
  color: var(--light);
  border-top: .2rem solid var(--toast-accent);
  visibility: hidden;
  opacity: 0;
  z-index: 1;
  text-align: center;
  border-radius: .2rem;
  justify-content: center;
  align-items: stretch;
  width: 100%;
  padding: 1em;
  transition: visibility .2s, opacity .2s linear;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  &.show {
    visibility: visible;
    opacity: 1;
